import React from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { useAuth } from 'react-oidc-context';
	
	
function isFileImage(file) {
    const acceptedImageTypes = ['image/gif', 'image/jpeg', 'image/png'];
 
    return file && acceptedImageTypes.includes(file['type'])
}


const Product = () => {

    const auth = useAuth();
    const [product,useProduct] = useState({
        name: "",
        quantity: "",
        prize: "",
        discount: "",
        description: "",
        image: ""
    })

    const HandleChange = (e) => {
        useProduct({
            ...product, [e.target.name] : e.target.value
        })
    }

    useEffect (() => {
      document.title="Song Demo"
      
  })

    const SendProduct = async (e) => {
        const request = {
            method: 'GET',
            statusCode: 200,
            headers: {
                'Access-Control-Allow-Origin' : 'origin',
                'Access-Control-Allow-Headers':'Content-Type,X-Amz-Date,Authorization,X-Api-Key,X-Amz-Security-Token',
                'Access-Control-Allow-Credentials' : true,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + auth.user.access_token
        },
          // body:product
        }
        await fetch('http://localhost:8321/product/hello',request)
            .then(response => response.json())
            .then(data => {
                console.log(data)
            }
            )
            .catch((e)=>{
              console.log(e)
            })
    }

    const HandleImage = (e) => {
        const check = isFileImage(e.target.files[0])
        if (!check){
          alert("Insert image please")
        }
        useProduct({...product, [e.target.name] : !check ? URL.createObjectURL(e.target.files[0]): ""})
        console.log(product.image)
    }


    useEffect  (() => {
        document.title= "Shop:Product"
    })

  return (
    <div>
        <form class="form-horizontal">
<fieldset>

<legend>PRODUCTS</legend>

<form>
  <div class="form-group">
    <label for="formGroupExampleInput">Product Name</label>
    <input type="text" class="form-control" id="formGroupExampleInput" placeholder="Product Name"
     value = {product.name} onChange = {HandleChange} name = "name"/>
  </div>
  <div class="form-group">
    <label for="formGroupExampleInput2">Product Quantity</label>
    <input type="text" class="form-control" id="formGroupExampleInput2" placeholder="Product Quantity" 
    value = {product.quantity} onChange = {HandleChange} name = "quantity"/>
  </div>
  <div class="form-group">
    <label for="formGroupExampleInput">Product Prize</label>
    <input type="text" class="form-control" id="formGroupExampleInput" placeholder="Product Prize" 
    value = {product.prize} onChange = {HandleChange} name = "prize"/>
  </div>
  <div class="form-group">
    <label for="formGroupExampleInput2">Product Discount</label>
    <input type="text" class="form-control" id="formGroupExampleInput2" placeholder="Product Discount" 
    value = {product.discount} onChange = {HandleChange} name = "discount"/>
  </div>
  <div class="form-group">
    <label for="formGroupExampleInput2">Product Description</label>
    <input type="text" class="form-control" id="formGroupExampleInput2" placeholder="Product Description" 
    value = {product.description} onChange = {HandleChange} name = "description"/>
  </div>
</form>

<div class="form-group">
  <label class="col-md-4 control-label" for="filebutton">Images</label>
  <div class="col-md-4">
    <input id="filebutton" name="image" class="input-file" accept="image/*" type="file" onChange = {HandleImage} ></input>
  </div>
</div>

<div class="form-group">
  <label class="col-md-4 control-label" for="singlebutton">Single Button</label>
  <div class="col-md-4">
    <button id="singlebutton" name="singlebutton" class="btn btn-primary" onClick={SendProduct} >Button</button>
  </div>
</div>

</fieldset>
</form>

    </div>
  )
}

export default Product